import s from './Login.module.css';
import usersApi from "../../services/user";
import { useFormik } from "formik";
import * as Yup from 'yup';
import {toast} from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

toast.configure();

const Login = ({setToken}) => {

    const initialValues = {
        login:'',
        password:''
    }

    const validationSchema = Yup.object().shape({
        login: Yup.string().min(2).required("Required"),
        password: Yup.string().required("Required"),
    })

    const onSubmit = async (values, onSubmitProps) => {
        try {
            const response = await usersApi.userLogin(values)
            setToken(response.token)
            toast.success('Login success',{
                position:'top-center'
            })
            onSubmitProps.resetForm();
        }catch (e) {
            toast.error(e.message, {
                position:'top-center'
            })
        }
    }

    const formik = useFormik({
        initialValues,
        validationSchema,
        onSubmit
    })

    return(<div className={s.login}>
      <form className={s.formStyle} onSubmit={formik.handleSubmit}>
          <div>
              <label htmlFor='login' className={s.label}>Login</label>
              <input type='text' className={s.input} {...formik.getFieldProps("login")}/>
              { formik.errors.login ? <span>{ formik.errors.login }</span> : null }
          </div>
          <div>
              <label htmlFor='password' className={s.label}>Password</label>
              <input type='password' className={s.input} {...formik.getFieldProps("password")}/>
              { formik.errors.password ? <span>{ formik.errors.password }</span> : null }
          </div>
          <div>
              <button type='submit' disabled={!formik.isValid} className={s.submit}>Login</button>
          </div>
      </form>
    </div>)
}

export default Login;